<template>
    <div class="view-head">
        <h2 class="head-logo"><span>ITTN Management Platform</span></h2>
        <div class="head-con"></div>
        <div class="head-user-wrap">
            <img class="head-user-img" :src="user_img_src" alt="">
            <span class="head-user-name" v-text="user_name"></span>
            <span class="operate-item" @click="exitHandle">退出</span>
        </div>
    </div>
</template>
<script>
    import storage from '../assets/tool/storage'
    export default {
        name: 'head-wrap',
        data () {
            return {
                user_name: '',
                user_img_src: 'https://img.alicdn.com/tps/TB1zBLaPXXXXXXeXXXXXXXXXXXX-121-59.svg'
            }
        },
        created () {
            this.fetchUserInfo();
        },
        methods: {
            fetchUserInfo () {
                var user = storage.dataToSessionStorageOperate.achieve('user');
                this.user_name = user && user.user_name;
            },
            exitHandle () {
                this.$confirm('是否安全退出?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    storage.dataToSessionStorageOperate.clear();
                    this.$message({
                        showClose: true,
                        message: '成功退出',
                        type: 'success'
                    });
                    this.$router.push("/login");
                }).catch(() => {});
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/define';
    .view-head{
        z-index: 1;
        @extend %df;
        height: 50px;
        align-items: center;
        background-color: #fff;
        border-bottom: 1px solid #f2f2f2;
    }
    .head-logo{
        @extend %f18;
        @extend %df;
        @extend %cp;
        height: 50px;
        justify-content: center;
        align-items: center;
        color: #000;
        padding-left: 20px;
    }
    .head-con{
        @extend %df1;
    }
    .head-user-wrap{
        @extend %df;
        @extend %cp;
        @extend %h100;
        @extend %pr;
        align-items: center;
        padding: 0 20px;
    }
    .head-user-img{
        @include br(50%);
        width: 30px;
        height: 30px;
        background-color: #5e7885;
    }
    .head-user-name{
        @extend %f18;
        @extend %c6;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
    }
    .head-user-operate{
        @extend %pa;
        @extend %l50;
        margin-left: -50px;
        width: 100px;
        top: 100%;
        border: 1px solid #ddd;
    }
    .operate-item{
        @extend %f14;
        @extend %c9;
        margin-left: 10px;
        &:hover{
            color: red;
        }
    }
</style>



