<template>
    <div>
        <header-wrap></header-wrap>
        <div class="view-body">
            <transition :name="animateName">
                <router-view class="body-inner"></router-view>
            </transition>
        </div>
        <core-menu></core-menu>
    </div>
</template>
<script>
    import MenuTool from '../../components/menu-tool.vue'
    import HeaderWrap from '../../components/header-wrap.vue'
    import CoreMenu from '../../components/core-meun.vue'
    import gesture from '../../assets/tool/gesture'
    import type from '../../store/mutation-types'
    export default {
        name: 'home',
        data () {
            return {
                is_remember: true,
                user_name: '',
                user_password: '',
                user_code: '',
                is_user_code: false
            }
        },
        computed: {
            animateName () {
                return this.$store.state.animate_name;
            }
        },
        created () {
            this.$nextTick(() => {
                this.bindGestureMobile(); /**绑定手势*/
            })
        },
        methods: {
            /**绑定手势*/
            bindGestureMobile () {
                let _this = this;
                gesture(this.$el,{
                    upCallBackFun (event) {
                        Math.abs(event.y) > 20 && _this.$store.commit(type.SET_CORE_MENU, true);
                    },
                    downCallBackFun (event) {
                        Math.abs(event.y) > 20 && _this.$store.commit(type.SET_CORE_MENU, false);
                    },
                });
            },
            loginHandle () {
                this.is_user_code = true;
                if ( this.checkInput() ) return;
                this.$message({
                    msg: '正在登录中',
                    type: 'loading'
                });
            },
            checkInput () {
                if ( !this.user_name ) {
                    this.$message({
                        msg: '请输入昵称或邮箱',
                        type: 'danger'
                    });
                    return true;
                }
                if ( !this.user_password ) {
                    this.$message({
                        msg: '请输入密码',
                        type: 'danger'
                    });
                    return true;
                }
                if ( this.is_user_code && !this.user_code ) {
                    this.$message({
                        msg: '请输入验证码',
                        type: 'danger'
                    });
                    return true;
                }
                return false;
            }
        },
        components: {
            MenuTool,
            HeaderWrap,
            CoreMenu
        }
    }
</script>
<style lang="scss">
    @import "../../assets/scss/define";
    .view-body{
        @extend %df1;
        @extend %df;
        @extend %pr;
    }
    .body-inner{
        @extend %pa;
        top: 10px;
        left: 10px;
        bottom: 10px;
        right: 10px;
        @extend %df;
        @extend %df1;
    }
    .fade-enter-active {
        transition: all .5s
    }
    .fade-enter, .fade-leave-active {
        opacity: 0;
        transform: rotate( -70deg ) translateX( 300px );
    }
</style>



