<template>
    <div class="input-wrap"
         :class="{'active':is_active || input_value, 'open': is_open}">
        <label class="input-prompt">{{placeholder}}</label>
        <i class="input-bar"></i>
        <input class="input-con" :maxlength="max" v-if="type === 'text'" type="text" v-model="input_value" @focus="is_active = true" @blur="is_active = false">
        <input class="input-con" :maxlength="max" v-if="type === 'password'" type="password" v-model="input_value" @focus="is_active = true" @blur="is_active = false">
        <i class="input-clear" @click="input_value = ''" v-show="input_value">
            <svg class="clear-icon">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#clear-icon"></use>
            </svg>
        </i>
    </div>
</template>
<script>
    export default {
        name: 'user-input',
        props: {
            type: { default: 'text'},
            placeholder: { default: 'PLEASE'},
            value: { default: '' },
            max: { default: '' },
        },
        created () {
            this.input_value = this.value;
        },
        data () {
            return {
                is_active: false,
                input_value: '',
                is_open: false
            }
        },
        watch: {
            value (val) {
                this.input_value = val
            },
            input_value (newVal) {
                this.$emit('input', newVal);
            }
        }
    }
</script>
<style lang="scss">
    @import "../assets/scss/define";
    .select-box{
        @extend %pa;
        @extend %r0;
        @extend %l0;
        @extend %t50;
        @extend %oya;
        @extend %f14;
        @extend %c6;
        @include tst(height,.3s);
        margin-top: 10px;
        transform: translate3d(0,-50%,0);
        height: 0;
        background-color: #fff;
        border-right: 1px solid #ddd;
        border-left: 1px solid #ddd;
    }
    .select-item{
        @extend %db;
        @extend %bsb;
        @extend %cp;
        @include tst2(background-color,.3s,color,.3s);
        padding-left: 16px;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid #ddd;
        background-color: #f5f5f5;
        &:first-child{
            border-top: 1px solid #ddd;
        }
        &:hover{
            @extend %cfff;
            background-color: $mc;
        }
    }
    .input-wrap{
        @extend %pr;
        @extend %df;
        height: 32px;
        padding: 20px 0 0;
        border-bottom: 1px solid #ddd;
        &.active{
            .input-prompt{
                @extend %f12;
                bottom: 26px;
                color: $mc;
            }
            .input-bar{
                @extend %l0;
                @extend %r0;
                @extend %w100;
                background-color: $mc;
            }
        }
        &.open{
            z-index: 1;
            .select-box{
                height: 160px;
            }
        }
    }
    .input-con{
        @extend %df1;
        @extend %pr;
        @extend %db;
        @extend %f16;
        @extend %c6;
        font-family: adele;
        border: none;
        background-color: transparent;
    }
    .input-prompt{
        @extend %pa;
        @extend %f14;
        @extend %c9;
        @extend %b0;
        @include tst(all,.3s);
        font-family: adele;
        height: 32px;
        line-height: 32px;
        &.active{
            @extend %f12;
            bottom: 26px;
            color: $mc;
        }
    }
    .input-clear{
        @extend %pr;
        @extend %cp;
        width: 32px;
        height: 32px;
    }
    .clear-icon{
        @extend %pa;
        @extend %t50;
        @extend %l50;
        width: 16px;
        height: 16px;
        margin-top: -8px;
        margin-left: -8px;
        fill: #ccc;
    }
    .input-bar{
        @extend %pa;
        @extend %l50;
        bottom: -2px;
        height: 2px;
        width: 2px;
        margin-left: -1px;
        @include tst(all,.3s);
        background-color: transparent;
    }
</style>



