<template>
    <div class="form-wrap"
         v-loading="is_loading"
         element-loading-text="loading...">
        <div class="form-header">
            <h3 class="form-header-title">{{_id ? '编辑：' : '新增：'}}</h3>
        </div>
        <div class="form-inner">
           <div class="form-content">
               <el-form ref="form" label-width="80px">
                   <el-form-item label="APP名称">
                       <el-input v-model="name" :disabled="!!_id"></el-input>
                   </el-form-item>
                   <el-form-item label="APP版本">
                       <el-input v-model="version"></el-input>
                   </el-form-item>
                   <el-form-item label="APP内容">
                       <el-input type="textarea" class="textarea" v-model="content"></el-input>
                   </el-form-item>
               </el-form>
           </div>
        </div>
        <div class="form-operate">
            <el-button plain type="primary" @click="submitHandle">立即提交</el-button>
            <div class="form-close-wrap" @click="closeHandle">
                <i class="form-close el-icon-close"></i>
            </div>
        </div>
    </div>
</template>
<script>
    import API from '../../../assets/api'
    export default {
        name: 'release-form',
        data() {
            return {
                content: '',
                name: '',
                version: '',
                is_loading: false,
                _id: '',
                env: ''
            }
        },
        created () {
            this._id = this.$route.params._id || '';
            this._id && this.fetchInfo();
            this.env = this.$route.query.env || 'test';
        },
        methods: {
            fetchInfo () {
                this.is_loading = true;
                API.Req_FetchToH5VersionInfo(this._id).then((result) => {
                    this.is_loading = false;
                    this.content = result.h5_content;
                    this.name = result.app_name;
                    this.version = result.h5_version;
                }).catch(() => {
                    this.is_loading = false;
                })
            },
            submitHandle() {
                if(!this.name) return this.$message({showClose: true, message: '请输入APP名称', type: 'error'});
                if(!this.version) return this.$message({showClose: true, message: '请输入APP版本', type: 'error'});
                if(!this.content) return this.$message({showClose: true, message: '请输入APP内容', type: 'error'});
                this.is_loading = true;
                if (this._id) {
                    API.Do_EditToH5Version(this._id, this.name.trim(), this.version.trim(), this.content.trim(), this.env).then(result => {
                        this.is_loading = false;
                        this.$message({showClose: true, message: '编辑成功', type: 'success'});
                        this.closeHandle();
                    }).catch((error) => {
                        this.is_loading = false;
                    })
                } else {
                    API.Do_AddToH5Version(this.name.trim(), this.version.trim(), this.content.trim(), this.env).then(result => {
                        this.is_loading = false;
                        this.$message({showClose: true, message: '新增成功', type: 'success'});
                        this.closeHandle();
                    }).catch((error) => {
                        this.is_loading = false;
                    })
                }
            },
            closeHandle () {
                window.history.go(-1);
            }
        }
    }
</script>



