<template>
    <transition
         enter-active-class="animated bounceIn"
         leave-active-class="animated bounceOut">
        <div class="core-menu animated"
             :class="{'active': is_active, 'active_after': is_active && is_active_after }"
             v-show="openOrCloseComputed">
            <div v-for="(item, index) in sortCoreArr($route.meta.core_center)"
                 :key="index"
                 class="core-item"
                 @click="onPressHandle(item)"
                 :title="item.title"
                 :class="item.css">
                <span>{{item.name}}</span>
            </div>
        </div>
    </transition>
</template>
<script>
    import type from '../store/mutation-types'
    import storage from '../assets/tool/storage'
    export default {
        name: 'core-menu',
        data () {
            return {
                is_active: false,
                is_active_after: false,
                core_arr: [
                    { name: 'Release', css: 'core-item-sub core-item-0', title: '发布' },
                    { name: 'User', css: 'core-item-sub core-item-1', title: '用户管理' },
                    { name: '3', css: 'core-item-sub core-item-2', title: '3' },
                    { name: '4', css: 'core-item-sub core-item-3', title: '4' },
                    { name: '5', css: 'core-item-sub core-item-4', title: '5' },
                    { name: '6', css: 'core-item-sub core-item-5', title: '6' },
                    { name: '7', css: 'core-item-sub core-item-6', title: '7' },
                    { name: 'Exit', css: 'core-item-sub core-item-7', title: '安全退出' },
                    { name: 'ITTN', css: 'core-item-8 core-center', title: '首页' },
                ],
            }
        },
        computed: {
            openOrCloseComputed () {
                setTimeout(() => {
                    this.is_active = this.$store.state.open_or_close;
                    setTimeout(() => {
                        this.is_active_after = this.$store.state.open_or_close;
                    },700)
                },100);
                return (this.$store.state.open_or_close || this.$route.meta.open_or_close);
            },
        },
        methods: {
            sortCoreArr (core_name) {
                this.selectArrItem(core_name);
                return this.core_arr;
            },
            onPressHandle ({name}) {
                var core_name = name.toLocaleLowerCase();
                this.selectArrItem(core_name);
                switch (core_name) {
                    case 'exit':
                        this.exitHandle();
                        break;
                    case 'ittn':
                        this.$router.push('/');
                        break;
                    default:
                        setTimeout(() => {
                            this.closeHandle();
                        },500);
                        this.$router.push('/'+ core_name);
                        break;
                }

            },
            selectArrItem (name) {
                var new_index = this.fetchArrItem('name', name),
                    old_index = this.fetchArrItem('css', 'core-item-8 core-center'),
                    css = this.core_arr[new_index].css;
                this.core_arr[new_index].css = this.core_arr[old_index].css;
                this.core_arr[old_index].css = css;
            },
            fetchArrItem (key, value) {
                var index = '';
                this.core_arr.forEach((item,i) => {
                    if(item[key].toLocaleLowerCase() === value.toLocaleLowerCase()) return index = i;
                });
                return index;
            },
            closeHandle () {
                this.$store.commit(type.SET_CORE_MENU, false);
            },
            exitHandle () {
                this.closeHandle();
                this.$confirm('是否安全退出?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    storage.dataToSessionStorageOperate.clear();
                    this.$message({
                        showClose: true,
                        message: '成功退出',
                        type: 'success'
                    });
                    this.$router.push("/login");
                });
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/define';
    .core-menu{
        z-index: 9;
        @extend %pf;
        @extend %cp;
        @extend %t50;
        @extend %l50;
        width: 50px;
        height: 50px;
        margin-left: -25px;
        margin-top: -25px;
        &.active_after{
            .core-item-sub{
                opacity: 1;
                z-index: 0;
                transition-delay: 0s !important;
            }
        }
        &.active{
            .core-item-0{
                @include tft(translate3d(0,-100px,0));
                transition-delay: 0s;
            }
            .core-item-1{
                @include tft(translate3d(70px,-70px,0));
                transition-delay: 0.1s;
            }
            .core-item-2{
                @include tft(translate3d(100px,0,0));
                transition-delay: 0.2s;
            }
            .core-item-3{
                @include tft(translate3d(70px,70px,0));
                transition-delay: 0.3s;
            }
            .core-item-4{
                @include tft(translate3d(0,100px,0));
                transition-delay: 0.4s;
            }
            .core-item-5{
                @include tft(translate3d(-70px,70px,0));
                transition-delay: 0.5s;
            }
            .core-item-6{
                @include tft(translate3d(-100px,0,0));
                transition-delay: 0.6s;
            }
            .core-item-7{
                @include tft(translate3d(-70px,-70px,0));
                transition-delay: 0.7s;
            }
            .core-item-sub{
                opacity: 1;
                z-index: 0;
            }
        }
    }
    .core-item{
        @extend %df;
        @extend %cp;
        @extend %pa;
        @extend %f14;
        @extend %fwb;
        @include br(50%);
        color: $mc;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        background-color: #fff;
        box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.2);
        span{
            @extend %cp;
        }
    }
    .core-item-sub{
        @include tst(all,.5s);
        opacity: 0;
        z-index: 1;
    }
    .core-center{
        color: #ffffff;
        z-index: 0;
        width: 50px;
        height: 50px;
        background-color: $mc;
        box-shadow: -4px 7px 46px 2px rgba(32, 160, 255, 0.4);
    }
</style>



