<template>
    <div class="body-menu">
        <div class="menu-wrap">
            <div class="menu-inner">
                <router-link
                    class="menu-item"
                    v-for="(item, index) in menu_arr"
                    :key="index"
                    :to="item.menu_link">
                    <span>{{item.menu_name}}</span>
                </router-link >
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        name: 'menu-tool',
        props: {
            menu_arr: { default: [] }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/define';
    .body-menu{
        @extend %oh;
        @extend %pr;
        z-index: 1;
        margin-right: 10px;
        width: 180px;
        height: 100%;
        border: 1px solid #f2f2f2;
        background-color: #fff;
    }
    .menu-wrap{
        width: 194px;
        overflow-y: auto;
        height: 100%;
    }
    .menu-inner{
        @extend %df;
        min-height: 100%;
        flex-direction: column;
        justify-content: center;
    }
    .menu-item{
        @extend %df;
        @extend %cp;
        width: 100%;
        justify-content: center;
        align-items: center;
        height: 40px;
        span{
            @extend %f16;
            @extend %c9;
        }
        &:hover{
            span{
                @extend %c6;
            }
        }
        &.router-link-active{
            @extend %fwb;
            span{
                color: $mc;
            }
        }
    }
</style>



