import axios from 'axios'
import config from '../config'
import cue_config from '../config/cue_config'
import router from '../../router/index'
import Vue from 'vue'
import storage from '../../assets/tool/storage'
axios.defaults.headers.post['Content-Type'] = "application/json; charset=utf-8";
const vue = new Vue();
/**公用请求ajax的方式*/
const ajax = (url, data, method = 'POST') => {
    var isGet = method === 'POST';
    console.log(url + '_请求的数据', data);
    return new Promise((resolve, reject) => {
        axios({
            url: url,
            method: method,
            baseURL: config.API_URL,
            timeout: 10000,
            data: isGet ? JSON.stringify(data) : '',
            params: isGet ? '' : JSON.stringify(data),
            responseType: 'json'
        }).then((result) => {
            /**连接超时*/
            if (result.statusText === 'ERR_CONNECT_FAILED' || result.status === -1) {
                vue.$message({showClose: true, message: cue_config.OVERTIME, type: 'error'});
                return reject(cue_config.OVERTIME);
            }
            var data = result.data;
            /**没有data数据*/
            if ( !data ) {
                vue.$message({showClose: true, message: cue_config.BUSY_SERVICE, type: 'error'});
                return reject(cue_config.BUSY_SERVICE);
            }
            /**成功*/
            if ( data.code === '0000' ) {
                return resolve(data.data);
            } else if ( data.code === '2000' || data.code === '2001' ) {
                vue.$message({showClose: true, message: data.info, type: 'error'});
                storage.dataToSessionStorageOperate.clear();
                return router.push('/login');
            } else {
                vue.$message({showClose: true, message: data.info, type: 'error'});
                return reject(data, data.code);
            }
        }).catch ((err) => {
            vue.$message({showClose: true, message: cue_config.REQUEST_FAIL, type: 'error'});
            return reject(cue_config.REQUEST_FAIL);
        })
    })
};


export default {
    /**
     * 用户登录
     * @user_name          [string]    用户名
     * @user_password      [string]    用户密码
     * */
    Do_UserLogin ( user_name, user_password ) {
        return ajax("manage/user/login", { user_name: user_name, user_password: user_password});
    },

    /**
     * 获取H5版本列表
     * @page_index      [string]    当前页
     * @page_size       [string]    每页数据大小
     * @key_words       [string]    搜索关键字
     * */
    Req_FetchH5VersionList ( page_index = '1', page_size = '10', key_words = '' ) {
        return ajax("manage/h5/list", { page_index, page_size, key_words });
    },

    /**
     * 新增H5版本
     * @app_name        [string]    app的名称
     * @h5_version      [string]    h5的版本号
     * @h5_content      [string]    h5的内容
     * */
    Do_AddToH5Version ( app_name = 'ittn', h5_version = '', h5_content = '' ) {
        return ajax("manage/h5/add", { app_name, h5_version, h5_content });
    },

    /**
     * 删除H5版本
     * @_id             [string]    app的ID
     * */
    Do_DeleteToH5Version ( _id ) {
        return ajax("manage/h5/delete", { _id });
    },

    /**
     * 批量删除H5版本
     * @_id_arr         [string]    app的ID的数组
     * */
    Do_BatchDeleteToH5Version ( _id_arr ) {
        return ajax("manage/h5/deletes", { _id_arr });
    },

    /**
     * 编辑H5版本
     * @_id             [string]    app的ID
     * @app_name        [string]    app的名称
     * @h5_version      [string]    h5的版本号
     * @h5_content      [string]    h5的内容
     * */
    Do_EditToH5Version ( _id, app_name, h5_version, h5_content ) {
        return ajax("manage/h5/edit", { _id, app_name, h5_version, h5_content });
    },

    /**
     * 查询H5版本
     * @_id             [string]    app的ID
     * */
    Req_FetchToH5VersionInfo ( _id ) {
        return ajax("manage/h5/info", { _id });
    },

    /**
     * 发布H5版本
     * @_id             [string]    app的ID
     * @app_name        [string]    app的名称
     * */
    Do_ReleaseH5Version ( _id, app_name = 'ittn' ) {
        return ajax("manage/h5/release", { _id, app_name });
    },

    /**
     * 获取用户信息列表
     * @page_index      [string]    当前页
     * @page_size       [string]    每页数据大小
     * @key_words       [string]    搜索关键字
     * */
    Req_FetchAppUserList ( page_index = '1', page_size = '10', key_words = '' ) {
        return ajax("manage/app_user/list", { page_index, page_size, key_words });
    },

    /**
     * 编辑H5版本
     * @_id             [string]    ID
     * @user_name       [string]    账户名
     * @user_emial      [string]    邮箱
     * */
    Do_EditToAppUser ( _id, user_name, user_email ) {
        return ajax("manage/app_user/edit", { _id, user_name, user_email });
    },

    /**
     * 查询用户信息
     * @_id             [string]    ID
     * */
    Req_FetchAppUserInfo ( _id ) {
        return ajax("manage/app_user/info", { _id });
    },

    /**
     * 删除用户信息
     * @_id             [string]    ID
     * */
    Do_DeleteAppUser ( _id ) {
        return ajax("manage/app_user/delete", { _id });
    },

    /**
     * 批量删除用户信息
     * @_id_arr         [string]    ID的数组
     * */
    Do_BatchDeleteAppUser ( _id_arr ) {
        return ajax("manage/app_user/deletes", { _id_arr });
    },

}



