<template>
    <div class="table-header">
        <h2 class="table-title" v-text="table_title"></h2>
        <el-button-group class="table-operate" v-if="table_operate_arr.length">
            <el-button
                plain
                v-for="(item, index) in table_operate_arr"
                :key="index"
                :type="item.type"
                size="small"
                @click.native="operateHandle(item)"
                :disabled="item.disabled"
                :icon="item.icon">{{item.txt}}
            </el-button>
        </el-button-group>
        <div class="table-search">
            <el-input placeholder="请输入内容" @input="inputHandle" v-model="input_value" class="input-with-select">
                <el-button slot="append" icon="el-icon-search" @click="searchHandle"></el-button>
            </el-input>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'table-header',
        data () {
            return {
                input_value: ''
            }
        },
        props: {
            table_title: { default: '标题' },
            table_search_placeholder: { default: '请输入' },
            table_operate_arr: { default: [] },
            value: { default: '' }
        },
        created(){
            this.input_value = this.value;
        },
        methods: {
            inputHandle () {
                this.$emit('input', this.input_value);
            },
            searchHandle () {
                this.$emit('search')
            },
            operateHandle (item) {
                this.$emit('operate', item)
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/define';
    .table-header{
        @extend %df;
        height: 50px;
        align-items: center;
        border: 1px solid #f2f2f2;
        background-color: #fff;
    }
    .table-title{
        @extend %f14;
        @extend %df1;
        color: #666;
        padding-left: 20px;
    }
    .table-search{
        @extend %df;
        width: 300px;
        padding-right: 20px;
    }
    .table-btn{
        margin-left: 20px;
    }
    .table-operate{
        margin-right: 20px;
    }
</style>



