<template>
    <div class="form-wrap"
         v-loading="is_loading"
         element-loading-text="loading...">
        <div class="form-header">
            <h3 class="form-header-title">{{_id ? '编辑：' : '新增：'}}</h3>
        </div>
        <div class="form-inner">
           <div class="form-content">
               <el-form ref="form" label-width="80px">
                   <el-form-item label="账户名">
                       <el-input v-model="user_name"></el-input>
                   </el-form-item>
                   <el-form-item label="邮箱">
                       <el-input v-model="user_email"></el-input>
                   </el-form-item>
               </el-form>
           </div>
        </div>
        <div class="form-operate">
            <el-button plain type="primary" @click="submitHandle">立即提交</el-button>
            <div class="form-close-wrap" @click="closeHandle">
                <i class="form-close el-icon-close"></i>
            </div>
        </div>
    </div>
</template>
<script>
    import API from '../../../assets/api'
    export default {
        name: 'user-form',
        data() {
            return {
                user_name: '',
                user_email: '',
                is_loading: false,
                _id: '',
            }
        },
        created () {
            this._id = this.$route.params._id || '';
            this._id && this.fetchInfo();
        },
        methods: {
            fetchInfo () {
                this.is_loading = true;
                API.Req_FetchAppUserInfo(this._id).then((result) => {
                    this.is_loading = false;
                    this.user_name = result.user_name;
                    this.user_email = result.user_email;
                }).catch(() => {
                    this.is_loading = false;
                })
            },
            submitHandle() {
                if(!this.user_name) return this.$message({showClose: true, message: '请输入帐户名', type: 'error'});
                if(!this.user_email) return this.$message({showClose: true, message: '请输入邮箱', type: 'error'});
                this.is_loading = true;
                API.Do_EditToAppUser(this._id, this.user_name.trim(), this.user_email.trim()).then(result => {
                    this.is_loading = false;
                    this.$message({showClose: true, message: '编辑成功', type: 'success'});
                    this.closeHandle();
                }).catch((error) => {
                    this.is_loading = false;
                })
            },
            closeHandle () {
                window.history.go(-1);
            }
        }
    }
</script>



