<template>
    <div class="view-wrap login-view">
        <div class="login-inner"
             v-loading="is_loading"
             element-loading-text="loading..."
             :class="{'active': is_user_code}">
            <h1 class="logo-name">ITTN</h1>
            <user-input class="user-input" v-model="user_name" placeholder="USERNAME"></user-input>
            <user-input class="user-input" type="password" v-model="user_password" placeholder="PASSWORD"></user-input>
            <div class="check-code-box" v-if="is_user_code">
                <div><user-input v-model="user_code" placeholder="VERIFICATION CODE" max="6"></user-input></div>
                <img title="看不清？换一张" class="check-code-img" src="" alt="">
            </div>
            <div class="user-relevant">
                <div @click="is_remember = !is_remember" class="remember-user" :class="{'remember-active-user': is_remember}">
                    <switch-button :switch_type="is_remember"></switch-button>
                    <span>Remember Me?</span>
                </div>
                <a href="#/retrieve" class="back-password">Forgot Password?</a>
            </div>
            <i class="login-btn" @click="loginHandle" :class="{'active': user_name && user_password && (!is_user_code || user_code) }">Sign In</i>
        </div>
    </div>
</template>
<script>
    import UserInput from '../../components/user-input.vue'
    import SwitchButton from '../../components/switch-button.vue'
    import API from '../../assets/api'
    import storage from '../../assets/tool/storage'
    export default {
        name: 'login',
        data () {
            return {
                is_remember: false,
                user_name: '',
                user_password: '',
                user_code: '',
                is_user_code: false,
                is_loading: false,
            }
        },
        components: {
            UserInput,
            SwitchButton
        },
        created () {
            this.fetchUserInfo();
        },
        methods: {
            /**登录*/
            loginHandle () {
                if(!this.user_name || !this.user_password) return;
                this.is_loading = true;
                API.Do_UserLogin(this.user_name, this.user_password).then((result) => {
                    this.is_loading = false;
                    storage.dataToSessionStorageOperate.save('user', result);
                    this.is_remember ? this.saveUserInfo(this.user_name, this.user_password) : this.clearUserInfo();
                    this.$router.push('main');
                }).catch((error) => {
                    this.is_loading = false;
                });
            },
            /**获取用户名密码*/
            fetchUserInfo () {
                var user_info = storage.dataToLocalStorageOperate.achieve('user_info') || '';
                this.is_remember = !!user_info;
                if (user_info){
                    this.user_name = user_info.user_name;
                    this.user_password = user_info.user_password;
                }
            },
            /**存储用户名密码*/
            saveUserInfo (user_name, user_password) {
                storage.dataToLocalStorageOperate.save('user_info', {
                    user_name,
                    user_password
                })
            },
            /**存储用户名密码*/
            clearUserInfo () {
                storage.dataToLocalStorageOperate.remove('user_info')
            }
        }
    }
</script>
<style lang="scss">
    @import "../../assets/scss/define";
    .login-inner{
        @extend %pa;
        @extend %bsb;
        @extend %t50;
        @extend %l50;
        @include tst(height,.5s);
        margin-top: -280px;
        margin-left: -200px;
        width: 400px;
        height: 480px;
        padding: 50px;
        -webkit-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
        -o-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
        box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
        background-color: #fff;
        &.active{
            height: 543px;
            .check-code-box{
                @extend %o1;
                height: 54px;
                margin: 10px 0 20px;
            }
        }
    }
    .check-code-box{
        @extend %df;
        @extend %oh;
        @extend %o0;
        @include tst(all,.5s);
        height: 0;
        > div{
            @extend %df1;
        }
    }
    .check-code-img{
        @extend %cp;
        width: 80px;
        height: 35px;
        margin-top: 18px;
        margin-left: 10px;
        background-color: #f5f5f5;
    }
    .logo-name{
        @extend %c0;
        letter-spacing: 2px;
        font-family: adele;
        line-height: 40px;
        font-size: 28px;
        margin-bottom: 30px;
    }
    .user-input{
        margin-bottom: 30px;
    }
    .login-btn{
        @extend %db;
        @extend %f16;
        @extend %tac;
        @extend %cfff;
        @extend %cp;
        @include tst(background-color,.3s);
        height: 40px;
        background-color: #bbb;
        line-height: 40px;
        margin: 60px 0;
        &.active{
            background-color: $mc;
        }
    }
    .user-link{
        @extend %c9;
        @extend %f12;
        span{
            color: $mc;
        }
    }
    .user-relevant{
        @extend %c9;
        @extend %pr;
        @extend %clearfix;
        @extend %f12;
        margin: 20px 0;
    }
    .remember-user{
        @extend %fl;
        @extend %cp;
        @extend %pr;
        @extend %df;
        justify-content: center;
        transition: color .5s;
        &.remember-active-user{
            color: $mc;
        }
        span{
            margin-left: 5px;
        }
    }
    .remember-type{
        @extend %pa;
        @extend %t50;
        @extend %l0;
        margin-top: -6px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        border: 1px solid #999;
        &:after{
            @extend %pa;
            @extend %t50;
            @extend %l50;
            margin-top: -3px;
            margin-left: -3px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            content: '';
            transition: background-color .5s;
            background-color: #fff;
        }
    }
    .back-password{
        @extend %fr;
        @extend %c9;
        &:hover{
            color: $mc;
        }
    }
</style>



