<template>
    <div class="home-wrap">
        <div class="home-inner"></div>
        <p class="home-prompt">温馨提示：鼠标上滑打开菜单，下滑关闭菜单哦~~~</p>
    </div>
</template>
<script>
    import type from '../../store/mutation-types'
    export default {
        name: 'home',
        created () {
            this.$store.commit(type.SET_CORE_MENU, true);
        }
    }
</script>
<style lang="scss">
    @import '../../assets/scss/define';
    .home-wrap{
        @extend %df;
        flex-direction: column;
    }
    .home-inner{
        @extend %df1;
    }
    .home-prompt{
        @extend %tac;
        @extend %f14;
        @extend %c6;
        margin-bottom: 30px;
    }
</style>



