<template>
    <div>
        <!--搜索操作-->
        <table-header
            v-model="key_words"
            @operate="operateHandle"
            @search="searchHandle"
            :table_operate_arr="table_operate_arr"
            table_title="用户管理"
            table_search_placeholder="请输入版本号">
        </table-header>
        <!--/搜索操作-->
        <!--table中心-->
        <div class="table-inner"
             v-loading="is_loading"
             element-loading-text="loading...">
            <div class="table-content">
                <el-table
                    ref="multipleTable"
                    :data="data_arr"
                    tooltip-effect="dark"
                    @selection-change="selectHandle"
                    :row-class-name="tableRowClassName"
                    style="width: 100%">
                    <el-table-column
                        type="selection"
                        align="center"
                        width="55">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="user_name"
                        label="帐户名">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="user_email"
                        label="邮箱">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="注册日期">
                        <template slot-scope="scope">
                            <span>
                                <i class="el-icon-time"></i>
                                <span style="margin-left: 10px">{{ scope.row.user_date | format('yyyy-MM-dd') }}</span>
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        width="240"
                        align="center"
                        label="操作">
                        <template slot-scope="scope">
                            <el-button size="small" @click="editHandle(scope.row._id)" type="text">编辑</el-button>
                            <el-button size="small" @click="deleteHandle(scope.row)" type="text">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <!--/table中心-->
        <div class="table-pagination">
            <el-pagination
                @current-change="currentChangeHandle"
                :current-page="page_index"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="page_size"
                layout="total, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
       <transition
           enter-active-class="animated rollIn"
           leave-active-class="animated rollOut">
           <router-view class="table-form"></router-view>
       </transition>
    </div>
</template>
<script>
    import TableHeader from '../../../components/table-header.vue'
    import API from '../../../assets/api'
    export default {
        name: 'release-con-wrap',
        data () {
            return {
                data_arr: [],
                table_operate_arr: [
                    { txt: '批量', type: 'danger ', icon: 'el-icon-delete',  disabled: true},
                ],
                key_words: '',
                page_index: 1,
                page_size: 10,
                total: 0,
                is_loading: false,
                select_arr: []
            }
        },
        mounted () {
            this.fetchData();
        },
        watch: {
            '$route': 'fetchData',
            select_arr (new_value, old_value) {
                this.table_operate_arr[0].disabled = !new_value.length;
            }
        },
        methods:{
            tableRowClassName({row}) {
                return row.h5_is_release ? 'release-row' : '';
            },
            /**获取数据*/
            fetchData (route) {
                this.is_loading = true;
                var key_words = route ? route.query.key_words: this.$route.query.key_words;
                var page_index = route ? route.query.page_index: this.$route.query.page_index;
                this.page_index = +page_index || 1;
                API.Req_FetchAppUserList(this.page_index, this.page_size, key_words).then((result) => {
                    this.data_arr = result.arr;
                    this.total = result.total;
                    this.is_loading = false;
                }).catch((error) => {
                    this.is_loading = false;
                })
            },
            /**搜索*/
            searchHandle () {
                this.page_index = 1;
                this.$route.query.key_words ? this.$router.push('/user/list?page_index=' + this.page_index + '&key_words=' + this.key_words) :
                    this.key_words ? this.$router.push('/user/list?page_index=' + this.page_index + '&key_words=' + this.key_words) : '';
            },
            /**选择*/
            selectHandle (rows) {
                this.select_arr = rows;
            },
            /**跳页*/
            currentChangeHandle (val) {
                this.page_index = val;
                this.key_words ? this.$router.push('/user/list?page_index=' + this.page_index + '&key_words=' + this.key_words)
                    : this.$router.push('/user/list?page_index=' + this.page_index);
            },
            /**操作*/
            operateHandle (item) {
                switch (item.txt){
                    case '批量':
                        return this.deletesHandle();
                }
            },
            /**删除*/
            deleteHandle ({_id, user_email}) {
                this.$confirm('确定是否删除'+ user_email +'的信息?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.is_loading = true;
                    API.Do_DeleteAppUser(_id).then((result) => {
                        this.is_loading = false;
                        this.$message({showClose: true, message: '删除成功', type: 'success'});
                        this.fetchData();
                    }).catch((error) => {
                        this.is_loading = false;
                    })
                }).catch(() => {});
            },
            /**批量删除*/
            deletesHandle () {
                this.$confirm('确定是否删除'+ this.select_arr.length +'条数据?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.is_loading = true;
                    API.Do_BatchDeleteAppUser(this.select_arr).then((result) => {
                        this.is_loading = false;
                        this.$message({showClose: true, message: '删除成功', type: 'success'});
                        this.fetchData();
                    }).catch((error) => {
                        this.is_loading = false;
                    })
                }).catch(() => {});
            },
            /**编辑*/
            editHandle (_id) {
                this.$router.push('/user/list/edit/'+ _id)
            }
        },
        components: {
            TableHeader
        }
    }
</script>



