<template>
    <div id="app">
        <transition :name="animateName">
            <router-view class="view-wrap"></router-view>
        </transition>
        <svg-template></svg-template>
    </div>
</template>
<script>
    import SvgTemplate from './components/svg-template.vue'
    export default {
        name: 'app',
        computed: {
            animateName () {
                return this.$store.state.animate_name;
            }
        },
        components: {
            SvgTemplate
        }
    }
</script>
<style lang="scss">
    @font-face {
        font-family: adele;
        src: url(./assets/fonts/adele-light-webfont.ttf);
    }
    @import "./assets/scss/reset";
    @import "./assets/scss/common";
    .view-wrap{
        @extend %df;
        flex-direction: column;
    }
    #app{
        background-image: url('./assets/images/bg.png');
        background-repeat: repeat;
    }
    .fade-out-enter-active, .fade-out-leave-active {
        transition: all .5s
    }
    .fade-out-enter, .fade-out-leave-active {
        opacity: 0;
        transform: translateX(-400px);
    }

    .fade-in-enter-active, .fade-in-leave-active {
        transition: all .5s
    }
    .fade-in-enter, .fade-in-leave-active {
        opacity: 0;
        transform: translateX(400px);
    }
</style>



