<template>
    <div>
        <menu-tool v-if="menu_arr.length" :menu_arr="menu_arr"></menu-tool>
        <div class="body-content">
            <transition
                enter-active-class="animated rotateInDownRight"
                leave-active-class="animated rotateOutDownRight">
                <router-view class="table-wrap"></router-view>
            </transition>
        </div>
    </div>
</template>
<script>
    import MenuTool from '../../components/menu-tool.vue'
    export default {
        name: 'release',
        data () {
            return {
                menu_arr: [
//                    { menu_name: 'TEST ENV', menu_link: '/release/test'},
//                    { menu_name: 'ONLINE ENV', menu_link: '/release/online'},
                ]
            }
        },
        components: {
            MenuTool
        }
    }
</script>



