<template>
    <div class="switch-button" :class="{'active': switch_type}">
        <i class="switch-btn"></i>
    </div>
</template>
<script>
    export default {
        name: 'switch-button',
        props: {
            switch_type: { default: false }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/define';
    .switch-button{
        @extend %pr;
        @extend %cp;
        @include br(16px);
        height: 10px;
        width: 16px;
        background-color: #999;
        &.active{
            background-color: $mc;
            .switch-btn{
                left: 7px;
            }
        }
    }
    .switch-btn{
        @extend %pa;
        @extend %t50;
        @include br(50%);
        @include tst(left,.5s);
        left: 1px;
        margin-top: -4px;
        width: 8px;
        height: 8px;
        background-color: #fff;
    }
</style>



