import Vue from 'vue'
import Router from 'vue-router'
import types from '../store/mutation-types'
import Tool from '../assets/tool/storage/index'

/**登录页*/
import Login from '../views/login/Login.vue'
import Main from '../views/main/Main.vue'
import Home from '../views/home/Home.vue'

import Release from '../views/release/Release.vue'
import ReleaseList from '../views/release/children/ReleaseList.vue'
import ReleaseForm from '../views/release/children/ReleaseForm.vue'

import User from '../views/user/User.vue'
import UserList from '../views/user/children/UserList.vue'
import UserForm from '../views/user/children/UserForm.vue'


import store from '../store/index.js'


Vue.use(Router);

let router = new Router({
    routes: [
        /**登录页*/
        {
            path: '/login',
            name: 'login',
            component: Login,
            meta: {
                status: 0
            },
        },
        /**首页*/
        { path: '/main', redirect: '/' },
        {
            path: '/',
            component: Main,
            children: [
                {
                    path: '/',
                    name: 'home',
                    component: Home,
                    meta: {
                        status: 1,
                        core_center: 'ITTN',
                        open_or_close: true
                    },
                },
                {
                    path: 'release',
                    component: Release,
                    children: [
                        { path: '/', redirect: 'list' },
                        {
                            path: 'list',
                            name: 'release-list',
                            component: ReleaseList,
                            meta: {
                                status: 200,
                                core_center: 'Release'
                            },
                            children: [
                                {
                                    path: 'add',
                                    name: 'release-add-form',
                                    component: ReleaseForm,
                                    meta:{
                                        status: 201,
                                        core_center: 'Release'
                                    }
                                },
                                {
                                    path: 'edit/:_id',
                                    name: 'release-edit-form',
                                    component: ReleaseForm,
                                    meta:{
                                        status: 202,
                                        core_center: 'Release'
                                    }
                                },
                            ]
                        },
                    ]
                },
                {
                    path: 'user',
                    component: User,
                    children: [
                        { path: '/', redirect: 'list' },
                        {
                            path: 'list',
                            name: 'user-list',
                            component: UserList,
                            meta: {
                                status: 300,
                                core_center: 'User'
                            },
                            children: [
                                {
                                    path: 'edit/:_id',
                                    name: 'user-edit-form',
                                    component: UserForm,
                                    meta:{
                                        status: 302,
                                        core_center: 'User'
                                    }
                                },
                            ]
                        },
                    ]
                }
            ]
        },

    ]
});

/**
 * 判断用户是否有登录，没有登录则跳转到登录页面
 * 如果用户已经登录，则回跳到登录的页的时候，要转到首页
 * */
router.beforeEach( (to, from, next) => {
    if( store ){
        if( from.meta.status > to.meta.status ) store.commit( types.SET_ANIMATE_NAME, 'vux-pop-out' );
        else store.commit( types.SET_ANIMATE_NAME, 'vux-pop-in' );
    }
    // var user = Tool.dataToSessionStorageOperate.achieve('user');
    // if ( !user && to.path !== '/login' ){
    //     next('/login');
    // } else if ( user && to.path === '/login'){
    //     next('/');
    // } else {
        next();
    // }
});
export default router




